<template>
	<view>
		<page-head :title="title"></page-head>
		<view class="uni-common-mt">
			<view>
				<map :latitude="latitude" :longitude="longitude" :markers="covers">
				</map>
			</view>
		</view>
		<view @bindtap="onGuideTap" data-latitude='39.92392' data-longitude='116.411885' data-bankName='最高人民检察院'>
		    <image src='../../../../static/img/map.png' class='list-guide-imgae'></image>
		    <text class='list-guide-text'>导航</text>
		</view>
	</view>
</template>
<script>
	import permision from "../../../../components/permission.js"
	export default {
		data() {
			return {
				title: 'map',
				latitude: 24.880014,
				longitude: 102.83297,
				covers: [{
					latitude: 24.880014,
					longitude: 102.83297,
					// #ifdef APP-PLUS
					iconPath: '../../../../static/img/map.png',
					// #endif
					// #ifndef APP-PLUS
					iconPath: '../../../../static/img/index.png',
					// #endif
				}, {
					latitude: 24.880014,
					longitude: 102.83297,
					// #ifdef APP-PLUS
					iconPath: '../../../../static/img/map.png',
					// #endif
					// #ifndef APP-PLUS
					iconPath: '../../../../static/img/index.png',
					// #endif
				}]
			}
		},
		methods: {
			onGuideTap (event) {
			    var lat = Number(event.currentTarget.dataset.latitude);
			    var lon = Number(event.currentTarget.dataset.longitude);
			    var bankName = event.currentTarget.dataset.bankname;
			    console.log(lat);
			    console.log(lon);
			    wx.openLocation({
			      type: 'gcj02',
			      latitude: lat,
			      longitude: lon,
			      name: bankName,
			      scale: 28
			    })
			}
		},
		onLoad() {
			wx.getSystemInfo({
			    success: (res) => {
			        let ww = res.windowWidth;
			        let wh = res.windowHeight;
					console.log(ww)
					console.log(wh)
			    }
			}),
			uni.getLocation({
				type:'gcj02',
			    success: (res) => {
			        // this.hasLocation = true;
					this.latitude = res.latitude
					this.longitude = res.longitude
					console.log(res.latitude)
					console.log(res.longitude)
			        // this.location = formatLocation(res.longitude, res.latitude);
			    },
			    fail: (err) => {
					console.log('获取失败')
				},
			})
			
		}
	}
</script>
<style>
	map {
		width: 100%;
		height: 1000rpx;
	}
	
	.list-guide-imgae{
	  height: 70rpx;
	  width: 70rpx;
	  margin-right: 20px;
	  vertical-align: middle;
	}
	.list-guide-text{
	  vertical-align: middle;
	  line-height: 90rpx;
	  font-size: 35rpx;
	}
</style>
